<template>
  <div
    class="modal fade"
    ref="modal"
    id="couponModal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="couponModal"
    aria-hidden="true"
    data-bs-backdrop="static"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">
            <span v-if="isCreate"></span>
            <span v-else></span>
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
            @click="$emit('clearItem')"
          ></button>
        </div>
        <div class="modal-body">
          <div class="mb-3">
            <label for="title"></label>
            <input
              type="text"
              class="form-control"
              id="title"
              v-model="couponInfo.title"
              placeholder=""
            />
          </div>
          <div class="mb-3">
            <label for="code"></label>
            <input
              type="text"
              class="form-control"
              id="code"
              v-model="couponInfo.code"
              placeholder=""
            />
          </div>
          <div class="mb-3">
            <label for="dueDate"></label>
            <DatePicker class="inline-block h-full" v-model="dueDate">
              <template v-slot="{ inputValue, togglePopover }">
                <div class="flex items-center">
                  <input
                    id="dueDate"
                    :value="inputValue"
                    class="form-control"
                    @focus="togglePopover()"
                  />
                </div>
              </template>
            </DatePicker>
          </div>
          <div class="mb-3">
            <label for="percent"></label>
            <input
              type="number"
              class="form-control"
              id="percent"
              min="0"
              max="100"
              v-model.number="couponInfo.percent"
              placeholder=""
            />
          </div>
          <div class="mb-3">
            <div class="form-check">
              <input
                class="form-check-input"
                type="checkbox"
                :true-value="1"
                :false-value="0"
                v-model="couponInfo.is_enabled"
                id="isEnabled"
              />
              <label class="form-check-label" for="isEnabled">  </label>
            </div>
          </div>
        </div>

        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-primary"
            @click="$emit('updateCoupon', couponInfo)"
            :disabled="!isButtonValid"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import modalMixin from '@/mixins/modalMixin';
import { DatePicker } from 'v-calendar';

export default {
  props: {
    isCreate: {
      type: Boolean,
    },
    couponDetail: {
      type: Object,
    },
  },
  emits: ['updateCoupon', 'clearItem'],
  components: {
    DatePicker,
  },
  mixins: [modalMixin],
  computed: {
    isButtonValid() {
      const requireItem = ['code', 'percent', 'title', 'due_date'];
      return requireItem.every((item) => this.couponInfo[item]);
    },
  },
  data() {
    return {
      couponInfo: {},
      dueDate: '',
      date: '',
    };
  },
  watch: {
    couponDetail(val) {
      this.couponInfo = { ...val };
      const date = this.couponInfo.due_date;
      this.dueDate = date ? new Date(date) : new Date();
    },
    dueDate() {
      const dateISOString = this.dueDate.toISOString();
      const date = dateISOString.split('T')[0];
      this.couponInfo.due_date = new Date(`${date} 23:59:59`).getTime();
    },
  },
};
</script>
